import React, { Component } from 'react';
import classes from './page5.less';
import Map from '../map/map';


class Page extends Component {
  constructor(props) {
    super(props);

  }
  render() {
    return (
        <div className="section">
          <Map className={classes.map}/>
        </div>
    );
  }
}

export default Page;



