import Layer from './layer';
import educationIcon from '../../../imgs/education.png';
import medicalIcon from '../../../imgs/medical.png';
import BMap from 'bmap';

class Search {
  constructor(map: any, layer: Layer) {
    this.map = map;
    this.layer = layer;
    this.localSearch = new BMap.LocalSearch(this.map, {
      //renderOptions: {map: this.map, autoViewport: false},
      onSearchComplete: this.onSearchComplete.bind(this)
    });
  }
  onSearchComplete(results) {
    if (this.localSearch.getStatus() === BMAP_STATUS_SUCCESS){
      // 判断状态是否正确
      let iconImage = null;
      if (results.keyword === '学校') {
        iconImage = educationIcon;
      }
      if (results.keyword === '医院') {
        iconImage = medicalIcon;
      }
      let pois = [];
      for (let i = 0; i < results.getCurrentNumPois(); i++) {
        pois.push(results.getPoi(i));
      }
      pois.forEach(function (poi) {
        let icon = new BMap.Icon(iconImage,
            new BMap.Size(30, 30),
            {imageSize: new BMap.Size(30, 30)});
        let marker = new BMap.Marker(poi.point, {
          icon: icon,
          title: poi.title
        });
        marker.addEventListener('click', function () {
          let content = '地址: ' + poi.address;
          let infoWindow = new BMap.InfoWindow(content, {
            title: poi.title
          });  // 创建信息窗口对象
          marker.openInfoWindow(infoWindow);
        });
        this.layer.addOverlay(marker);
      }, this);
    }
  }
  searchAround(point) {
    this.localSearch.clearResults();
    this.layer.clearOverlays();
    let circle = new BMap.Circle(point,1000,{fillColor:"blue", strokeWeight: 1 ,fillOpacity: 0.3, strokeOpacity: 0.3});
    this.layer.addOverlay(circle);
    this.localSearch.searchNearby('医院', point, 1000);
    this.localSearch.searchNearby('学校', point, 1000);
  }
}

export default Search;


