import garbageQuantity from '../../data/garbage';
import React, { Component } from 'react';
import classes from './page2.less';
import EChart from '../echarts/echarts';

const years = Object.keys(garbageQuantity);
const cities = Object.keys(garbageQuantity[years[0]]);

const series = cities.map(function (city) {
  let data = years.map(function (year) {
    return garbageQuantity[year][city];
  });
  return {
    name: city,
    type: 'line',
    stack: '总量',
    areaStyle: {normal: {}},
    data: data
  };
});

const option = {
  title: {
    text: '垃圾围城(x万吨)'
  },
  tooltip : {
    trigger: 'axis'
  },
  legend: {
    data: cities
  },
  grid: {
    left: '3%',
    right: '4%',
    bottom: '3%',
    containLabel: true
  },
  xAxis: [
    {
      type : 'category',
      boundaryGap : false,
      data: years
    }
  ],
  yAxis: [
    {
      type : 'value'
    }
  ],
  series: series
};

const style = {
  width: '100%',
  height: '80%'
};

class Page2 extends Component {
  render() {
    return (
        <div className="section">

          <EChart ref='echart' option={option} style={style} />
    
          <div className={classes.garbageText}>
            Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.
          </div>
        </div>
    );
  }
}

export default Page2;



