import adminsInfo from './administratives';
import landfillsInfo from './landfill';

import _ from 'lodash';

const admins = adminsInfo.map(a => a.name);
const landfills = _.keys(landfillsInfo);

const adm2landfills = _.reduce(admins, (memo, adm) => {
  memo[adm] = _.sampleSize(landfills, 3);
  return memo;
}, {});

export default adm2landfills;


