import React, { Component } from 'react';
import { findDOMNode } from 'react-dom';
import BMap from 'bmap';
import _ from 'lodash';
import landfillIcon from '../../../imgs/landfill.png';
import landfills from '../../data/landfill';
import administratives from '../../data/administratives';
import adm2landfills from '../../data/adm2landfills';
import Layer from './layer';
import SearchUtils from './search';

function createLandfillContent(l) {
  const landfillInfo = landfills[l];
  return Object.keys(landfillInfo).reduce(function (memo, key, index) {
    if (key === 'pos') return memo;
    let content = index == 0 ? '' : '<br>';
    content += key + ': ' + landfillInfo[key];
    return memo + content;
  }, '');
}



const defaultCenter = [114.119432, 22.571383];
const styleJson = [
  {
    "featureType": "poi",
    "elementType": "all",
    "stylers": {
      "color": "#ffffff",
      "visibility": "off"
    }
  },
  {
    "featureType": "road",
    "elementType": "all",
    "stylers": {
      "color": "#ffffff",
      "visibility": "off"
    }
  },
  // {
  //   "featureType": "background",
  //   "elementType": "all",
  //   "stylers": {
  //     "color": "#ffffff"
  //   }
  // },
  {
    "featureType": "administrative",
    "elementType": "all",
    "stylers": {
      "color": "#ffffff",
      "visibility": "off"
    }
  }
];


class Map extends Component {
  
  constructor(props) {
    super(props);
  }
  render() {
    return (
        <div ref='visMap' {...this.props}>
        </div>
    );
  }
  componentDidMount() {
    setTimeout(this.init.bind(this), 1);
  }
  
  init() {
    this.map = new BMap.Map(findDOMNode(this.refs.visMap), {
      enableMapClick: false
    });
    this.center = new BMap.Point(defaultCenter[0], defaultCenter[1]);

    this.boundaryLayer = new Layer(this.map);
    this.landfillLayer = new Layer(this.map);
    this.searchLayer = new Layer(this.map);

    this.searchUtils = new SearchUtils(this.map, this.searchLayer);

    this.map.setMapStyle({
      styleJson: styleJson
    });
    this.map.centerAndZoom(this.center, 12);
    // set events
    this.map.disableDragging(); //启用地图拖拽事件，默认启用(可不写)
    this.map.disableDoubleClickZoom(); //启用鼠标双击放大，默认启用(可不写)

    // add ctrl
    //向地图中添加缩放控件
    this.map.addControl(new BMap.NavigationControl());
    // //向地图中添加缩略图控件
    // let ctrl_ove = new BMap.OverviewMapControl({
    //   anchor:BMAP_ANCHOR_BOTTOM_RIGHT,
    //   isOpen:1
    // });
    // this.map.addControl(ctrl_ove);
    // //向地图中添加比例尺控件
    // let ctrl_sca = new BMap.ScaleControl({
    //   anchor:BMAP_ANCHOR_BOTTOM_LEFT
    // });
    // map.addControl(ctrl_sca);
    this.drawBoundrayLayer();
    this.drawLandfillLayer();
  }
  drawBoundrayLayer() {
    administratives.forEach((a) => {

      let bdary = new BMap.Boundary();

      bdary.get(a.name, (rs) => { //获取行政区域
        let count = rs.boundaries.length; //行政区域的点有多少个
        for (let i = 0; i < count; i++) {
          let ply = new BMap.Polygon(rs.boundaries[i], {strokeWeight: 2, strokeColor: "#4A7300", fillColor: ""}); //建立多边形覆盖物
          this.boundaryLayer.addOverlay(ply); //添加覆盖物
          ply.addEventListener('click', this.onClickAdmin.bind(this, a.name));
        }
      });

      let pos = a.pos.split('|');
      let point = new BMap.Point(+pos[0], +pos[1]);
      let opts = {
        position : point,    // 指定文本标注所在的地理位置
        offset   : new BMap.Size(-20, -20)    //设置文本偏移量
      };
      let label = new BMap.Label(a.name, opts);  // 创建文本标注对象
      label.setStyle({
        color : 'white',
        fontSize : '12px',
        backgroundColor: '#6095DD',
        borderRadius: '4px',
        border: 0,
        padding: '4px 3px'
      });
      this.boundaryLayer.addOverlay(label);
    });
  }
  drawLandfillLayer() {
    _.each(landfills, (attrs, l) => {
      let pos = attrs.pos.split('|');
      let point = new BMap.Point(+pos[0], +pos[1]);
      let myIcon = new BMap.Icon(landfillIcon,
          new BMap.Size(30, 30),
          {imageSize: new BMap.Size(30, 30)});
      let marker = new BMap.Marker(point, {icon: myIcon});
      this.landfillLayer.addOverlay(marker);
      marker.addEventListener('click', this.onClickLandfill.bind(this, l));
    });

  }
  
  onClickAdmin(admin) {
    alert(admin);
  }
  onClickLandfill(landfill, e) {
    let p = e.target;
    let point = new BMap.Point(p.getPosition().lng, p.getPosition().lat);
    let infoWindow = new BMap.InfoWindow(createLandfillContent(landfill), {
      title: landfill
    });  // 创建信息窗口对象
    this.map.openInfoWindow(infoWindow, point); //开启信息窗口

    this.searchUtils.searchAround(point);
    this.map.centerAndZoom(point, 17);
  }
}

export default Map;


